import Popup from '../Popup/Popup.vue'

export default {
    components: {
        Popup
    },
    data () {
        return {
            isPopup: false,
            popupSettings: [],
            blocksSettings: []
        }
    },
    methods: {
        showPopup (settings) {
            this.isPopup = true
            this.popupSettings = settings
        },
        closePopup () {
            this.isPopup = false
        },
        getFieldsData () {
            //      .
            let someJson = `[ { "bText"    : " -        .           - , ,     ...    -  -  .",
                            "settings" : [ { "Name"       : "",
                                            "stringType" : "int",
                                            "fieldType"  : "input",
                                            "fieldValue" : "600",
                                            "fieldSize"  : 4 },
                                        { "Name"       : "",
                                            "stringType" : "int",
                                            "fieldType"  : "input",
                                            "fieldValue" : "500",
                                            "fieldSize"  : 4 } ] },
                            { "bText"    : " -        .           - , ,     ...    -  -  .",
                                "settings" : [ { "Name"       : "",
                                                "stringType" : "int",
                                                "fieldType"  : "select",
                                                "fieldValue" : [ "",
                                                                "" ] } ] } ]`
            this.blocksSettings = JSON.parse(someJson)
        }
    },
    mounted () {
        this.getFieldsData()
    }
}
